export default {
	props: {
		options: {
			type: Object,
			default: function(e) {
				return {}
			}
		}
	},
	data(){
		return{
			detailImgs:[]
		}
	},
	computed: {




















	},
	methods: {
		godetail(id,url) {
			console.log(id,url);
			if (!this.isgood) {
				var page = this.page;
				uni.navigateTo({
					url: `/pages/common/goods-detail?id=${id}&page=${page}&detailImgs=${this.detailImgs}`
				})
			} else {
				uni.navigateTo({
					url: `/pages/common/goods2-detail?id=${id}&url=${url}&detailImgs=${this.detailImgs}`
				})
			}
			this.globScrollTop = this.nowScroll;
		},
		close(e) {
			this.$emit('close');
		},
		bindClick() {
			this.$emit('click');
		}
	}
}
